<!--  -->
<template>
  <div id="_big-wheel">
    <canvas
      class="_big-wheel-canvas"
      ref="wheelCanvas"
      width="360"
      height="360"
      :style="`transform: rotate(${canvasAngle + targetAngle}deg);transition-duration: ${transitionDuration}s;`"
    ></canvas>
    <img
      :src="goBtnImg"
      class="_big-wheel-go-img"
      :class="goClassName"
      @click.stop="_onGo"
    >
  </div>
</template>

<script>
import { BtnImg } from './_base64.js'
export default {
  name: 'vueBigWheel',
  props: {
    prizeList: {  //
      type: Array
    },
    colors: { // 
      type: Array,
      default: () => [
        "#F47F45",
        "#FFA468"
      ]
    },
    transitionDuration: { //  s
      type: Number,
      default: () => 8
    },
    fontColor: { // 
      type: String,
      default: () => '#7D2A00'
    },
    fontSize: { // 
      type: String,
      default: () => '16px'
    },
    fontFamily: { // 
      type: String,
      default: () => 'Helvetica Neue,Tahoma,Arial,PingFangSC-Regular,Hiragino Sans GB,Microsoft Yahei,sans-serif'
    },
    goBtnImg: { // 
      type: String,
      default: () => BtnImg
    },
    goClassName: { // 
      type: String
    },
    strMaxLength: { // 
      type: Number,
      default: () => 0
    },
    strLineLength: {  // 
      type: Number,
      default: () => 0
    }
  },
  data() {
    return {
      startAngle: 0, //
      outsideRadius: 180, //
      insideRadius: 10, //
      textRadius: 140, //
      isOnRotate: false, // false 
      stayIndex: 0, // 
      targetAngle: 0, // 
    };
  },
  computed: {
    // canvas
    canvasAngle: function() {
      let _value = this.prizeList.length
      if(_value % 4 != 0) {
        return 0
      } else {
        let _a = _value / 4
        return _a % 2 == 0 ? 45/_a : 45
      }
    }
  },
  mounted() {
    this._drawWheelCanvas();
  },
  methods: {
    /**
     * 
     * count: Number 
     * targetIndex: Number 
     */
    rotateFunc(targetIndex, count = this.prizeList.length) {
      let _baseAngle = 360 / count
      let _angles
      if(this.targetAngle == 0) {
        //  = 270 - (-)* -  - canvas 
        _angles = (360 * 3 / 4 - (targetIndex - this.stayIndex) * _baseAngle - _baseAngle / 2) - this.canvasAngle;
      } else {
        //  
        _angles = -(targetIndex - this.stayIndex) * _baseAngle
      }
      this.stayIndex = targetIndex
      this.targetAngle += _angles + 360 * 8 //  


      setTimeout(() => {
        this.isOnRotate = false
        this.$emit('on-over')
      }, this.transitionDuration * 1000 + 100)
    },
    // go
    _onGo(event) {
      const _this = this
      if(_this.isOnRotate) return
      _this.isOnRotate = true
      _this.$emit('go-click', event)
    },
    // 
    _drawWheelCanvas() {
      // canvasAPIhttps://www.canvasapi.cn/CanvasRenderingContext2D/
      const _this = this;
      const canvas = _this.$refs.wheelCanvas;
      const ctx = canvas.getContext("2d");
      //
      let arc = Math.PI / (_this.prizeList.length / 2);
      let canvasW = canvas.width; // 
      let canvasH = canvas.height; // 
      //
      ctx.clearRect(0, 0, canvasW, canvasH);
      //strokeStyle 
      ctx.strokeStyle = "#FFBE04"; // 
      //font 
      ctx.font = `${_this.fontSize} ${_this.fontFamily}`;
      // 0

      // 
      let length = _this.prizeList.length;
      for (let i = 0; i < length; i++) {
        // 
        let angle = _this.startAngle + i * arc;
        // 
        ctx.fillStyle = _this.colors[i % 2];

        // 
        ctx.beginPath();
        /*
         * IOSQuartz2D
         * context.arc(x,y,r,sAngle,eAngle,counterclockwise);
         * x :x
         * y :x
         * sAngle,eAngle :
         * counterclockwise : ,False = true = 
         * */
        ctx.arc(
          canvasW * 0.5,
          canvasH * 0.5,
          _this.outsideRadius,
          angle,
          angle + arc,
          false
        );
        ctx.arc(
          canvasW * 0.5,
          canvasH * 0.5,
          _this.insideRadius,
          angle + arc,
          angle,
          true
        );
        ctx.stroke();
        ctx.fill();
        //Restore00
        ctx.save();

        /*----*/
        
        ctx.fillStyle = _this.fontColor;
        let rewardName = _this._limit(_this.prizeList[i],_this.strMaxLength);
        let line_height = 17;

        // translate (0,0) 
        let translateX =
          canvasW * 0.5 + Math.cos(angle + arc / 2) * _this.textRadius;
        let translateY =
          canvasH * 0.5 + Math.sin(angle + arc / 2) * _this.textRadius;
        ctx.translate(translateX, translateY);

        // rotate
        // angle +  arc / 2   + 90
        ctx.rotate(angle + arc / 2 + Math.PI / 2);
        

        if(rewardName.length > _this.strLineLength && _this.strLineLength != 0) {
          rewardName = rewardName.substring(0, _this.strLineLength) + "||" + rewardName.substring(_this.strLineLength);
          let rewardNames = rewardName.split("||");
          for (let j = 0; j < rewardNames.length; j++) {
            ctx.fillText(
              rewardNames[j],
              -ctx.measureText(rewardNames[j]).width / 2,
              j * line_height
            );
          }
        } else {
          ctx.fillText(rewardName, -ctx.measureText(rewardName).width / 2, 0);
        }
        //save()
        ctx.restore();

        /*--------*/
      }
    },
    /**
     * ...
     * value 
     * maxLength  ...
     * '',3'...'
     */
    _limit(value,maxLength = 0) {
      if(!value || maxLength == 0) return value
      return value.length > maxLength ? value.slice(0,maxLength-1) + '...' : value
    },
  }
};
</script>
<style  lang='scss' scoped>
#_big-wheel {
  position: relative;
  width: 100%;
  height: 100%;
  ._big-wheel-canvas {
    width: 100%;
    transition: transform cubic-bezier(.11,.77,.2,.94); // 
  }
  ._big-wheel-go-img {
    width: 112px;
    position: absolute;
    top: 28%;
    left: 50%;
    transform: translateX(-50%);
  }
}
</style>
